.skills-container {
  .skill-title {
    font-size: 1.4rem;
    font-weight: 400;
  }
  .skill-bar {
    display: flex;
    align-items: center;
    // .skill-text {
    // }
    .skill-progress {
      width: 100%;
      .progress {
        width: 100%;
        height: 0.5rem;
        background-color: red;
        background-color: #0381ff3f;
        margin-left: 1rem;
        position: relative;
        .inner-progress {
          position: absolute;
          background-color: #037fff;
          height: 100%;
          bottom: 0;
          left: 0;
        }
        //  we use '.inner-progress' to replace '&::after'
        // &::after {
        //   content: "";
        //   position: absolute;
        //   width: 70%;
        //   background-color: #037fff;
        //   height: 100%;
        //   bottom: 0;
        //   left: 0;
        // }
      }
    }
  }
}
